@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #FCD31C21;
    background-image: url(https://www.configur.tech/wp-content/uploads/2020/10/digital_agency_hero_bg_01.png);
}

.page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 50px;
    width: 100vw;

    &__header {
        height: 20px;
        box-shadow: 3px 3px 20px 3px rgb(0 0 0 / 20%);
        background-color: #ffffff;
        overflow: hidden;
        position: fixed;
        top: 0;
    }

    input {
        margin: 150px 10px 100px 10px;
    }
}
